.side_menu{height:100vh;background-color:#79c4ca}.side_menu .logo_container{text-align:center}.side_menu .logo_container .logo{height:3em;margin-top:1em}.side_menu .side_menu_list{list-style:none;text-align:center;cursor:pointer;padding:0}.side_menu .side_menu_list .side_menu_item{padding-top:1.5em;padding-bottom:1.5em;margin-top:1em}.side_menu .side_menu_list .side_menu_item:hover,.side_menu .side_menu_list .side_menu_item.active{background-color:#5cafb5}.side_menu .side_menu_list .side_menu_item:last-child{position:absolute;bottom:0;width:5%}

.chip_container{background-color:#608a8b;border-radius:2em;text-align:center;margin-top:1em;margin-bottom:1em}

.contact_list_item{display:flex;justify-content:space-between;border-bottom:2px solid #d9d9d9;padding:1em;cursor:pointer}.contact_list_item:hover{background-color:#ecf2f2}.contact_list_item:first-child{background-color:#ecf2f2}.contact_list_item .contact_item_data{display:flex;align-items:center}.contact_list_item .contact_item_data .user_profile{object-fit:cover;border-radius:50%;height:4em;width:4em}.contact_list_item .contact_item_data .image_placeholder{background-color:#ecf2f2;border-radius:50%;padding:1.5em;color:white;font-size:1em}.contact_list_item .contact_item_data .image_placeholder.notificaton_border{border:4px solid #608a8b}.contact_list_item .contact_item_data .contact_data_left{margin-left:0.5em}.contact_list_item .contact_item_data .contact_data_left .message_limit{display:inline-block;width:180px;white-space:nowrap;overflow:hidden !important;text-overflow:ellipsis}

.contact_list .contacts_list_header{display:flex;justify-content:space-between;align-items:center;background-color:#aaa;padding:1.5em;padding-left:1em;padding-right:1em;color:white;height:2vh}.contact_list .contacts_list_header .icon_size{font-size:20px}.contact_list .contact_list_container{height:90vh;overflow-y:auto;border-right:2px solid #d9d9d9}

.input,.input:focus,.input:active{background-color:white;border:none;width:100%;border-radius:5px;outline:none}

.icon_btn{background-color:transparent;border:none;cursor:pointer;font-size:20px}.icon_btn:focus{border:none;outline:none}

.chat_actions{display:flex;justify-content:space-between;background-color:#f0f0f0;padding:1em;position:-webkit-sticky;position:sticky;bottom:0}

.chat_container{display:grid;height:100vh;grid-template-rows:auto auto 10vh}.chat_container .chat_container_header{display:flex;justify-content:space-between;align-items:center;background-color:#79c4ca;padding:1.5em;padding-left:1em;padding-right:1em;color:white;height:2vh}.chat_container .chat_messaging_container{padding:2em;overflow:auto}.chat_container .chat_messaging_container .chat_start_heading{display:flex;justify-content:center;align-items:center;margin-bottom:2em}

.chat_message{display:flex;margin-bottom:2em}.chat_message .chat_bubble{position:relative;display:inline-block;background-color:#f0f3f9;padding:1em;border-radius:10px;margin-left:1em;margin-right:1em}.chat_message .chat_bubble::before{content:"";position:absolute;left:-1em;bottom:0;border:0 solid transparent;border-top:9px solid #f0f3f9;border-radius:0 20px 0;width:15px;height:30px;transform:rotate(145deg)}.chat_message .user_profile{object-fit:cover;border-radius:50%;height:3em;width:3em;align-self:flex-end}.chat_message .message_container .message_info{display:flex;justify-content:space-between;margin:0 1em 0 1em}.chat_message .reminder_item{display:flex;margin:1em 0em}.chat_message .reminder_item .reminder_content{margin:0 0 0 0.5em;align-self:center}.chat_message .status_container{background-color:white;padding:0.5em 2em;color:#e88282;display:inline-block;border-radius:10px}.chat_message .reminder_actions{display:flex;justify-content:space-between}.chat_message .reminder_actions .reminder_actions_info{display:flex;justify-content:space-between}.chat_message.sender{justify-content:flex-end}.chat_message.sender .chat_bubble{background-color:#e6f2f2}.chat_message.sender .chat_bubble::before{content:"";position:absolute;right:-1.2em;left:auto;bottom:0;border-radius:20px 0 20px;transform:rotate(225deg);border-top:9px solid #e6f2f2}

.main_grid_container{display:grid;grid-template-columns:5% 30% 65%}

